import React, { Component } from 'react';
import { ReactDOM } from 'react-dom';
import { Header, Section, renderCheckup, BackToMain } from './index';

let painLevel;

const Textbox = props => (
    <form>
        <textarea name="sideEffects"/>
    </form>
);

class PainLevelQuestions extends Component {
    render() {
        if (painLevel < 4) {
            return (
                <div>
                    <Section text="Does the pain impair your ability to perform daily activities such as working, socializing etc.?" />
                    <Section text="How many pills have you taken per day?" />
                    <Section text="How satisfied are you with your medication?" />
                    <Section text="Have you noticed any side effects? If you have, please describe them." answer={Textbox} />
                    <Section text="Do the side effects impair your ability to perform daily activities such as working, socializing etc.?" />
                    <SubmitButton />
                </div> 
            );
        } else if (painLevel < 7) {
            return (
                <div>
                    <Section text="How do you cope with the pain?" />
                    <Section text="Does the pain impair your ability to sleep?" />
                    <Section text="How many pills have you taken per day?" /> 
                    <Section text="Have you experienced any severe side effects (such as bleeding)?" />    
                    <SubmitButton />                                   
                </div>
            );
        } else if (painLevel >= 7) {
            return <Section text="Notification: In case of severe acute pain, contact the emergency number or your doctor immediately." />;
        } else {
            return null;
        }
    }
};

const handlePainLevel = (event) => {
    event.preventDefault();
    const radios = document.getElementsByName('pain');

    for (let i = 0; i < radios.length; i++) {
        if (radios[i].checked) {
            painLevel = radios[i].value;
        }
    }
    console.log('pain level is ', painLevel);
    renderCheckup();
};

const PainLevelRadios = props => (
    <form onSubmit={handlePainLevel}>
        <PainLevel level="0" />
        <PainLevel level="1" />
        <PainLevel level="2" />
        <PainLevel level="3" />
        <PainLevel level="4" />
        <PainLevel level="5" />
        <PainLevel level="6" />
        <PainLevel level="7" />
        <PainLevel level="8" />
        <PainLevel level="9" />
        <PainLevel level="10" /><br/>
        <button className="btn-default" type="submit">Select</button>
    </form>
);

class PainLevel extends Component {
    render() {
        return (
            <label className="radio-inline"><input type="radio" name="pain" value={this.props.level}/>{this.props.level}</label>
        );
    }
};

class PainExplanation extends Component {
    render() {
        if (painLevel === '0') {
            return <p>0: You have no pain at all.</p>
        } else if (painLevel === '1') {
            return <p>1: Your pain is hardly noticable.</p>
        } else if (painLevel === '2') {
            return <p>2: Your pain is mild and you are aware of your pain when you pay attention to it.</p>
        } else if (painLevel === '3') {
            return <p>3: Your pain is tolerable and you don’t notice it most of the time.</p>
        } else if (painLevel === '4') {
            return <p>4: Your pain is moderate and you are aware of it most of the time but you can continue most daily activities.</p>
        } else if (painLevel === '5') {
            return <p>5: Your pain is strong and distracting and you are unable to perform some daily activities.</p>
        } else if (painLevel === '6') {
            return <p>6: Your pain is intense and can cause you to think unclearly at times. You think about the pain all of the time and are unable to do many daily activities.</p>
        } else if (painLevel === '7') {
            return <p>7: Your pain is unmanageable and keeps you from doing most of your daily activities.</p>
        } else if (painLevel === '8') {   
            return <p>8: Your pain is horrible and talking and listening are hard because of it.</p>
        } else if (painLevel === '9') {    
            return <p>9: Your pain is so severe that you cannot tolerate it and are unable to talk or move much.</p>
        } else if (painLevel === '10') {
            return <p>10: You are unable to move and will go unconscious soon because of unimaginable pain.</p>
        } else {
            return null;
        }
    }
};

const Questions = props => (
    <div>
        <div id="question1">
            <Section text="What is your current pain level?" subtext={<PainLevelRadios />} />
            <PainExplanation />
            <PainLevelQuestions />
        </div>
    </div>
);

const SubmitButton = props => (
    <button onClick={renderThankYou}>Submit</button>
);

const ThankYouPage = props => (
    <div>
        <Header title="Thank you!" />
        <p>Your answers have been submitted.</p>
        <BackToMain />
    </div>
);

const renderThankYou = () => ReactDOM.render(<ThankYouPage />, root);

export default class CheckupPage extends Component {
    render() {
        return (
            <div>
            <Header title="New checkup" />
            <Questions />
            <BackToMain />
        </div>
        )
    }
};


